<template>
  <div class="container">
    <b-col sm="5">
      
      <b-button v-if="form_control.state" @click="changeForm()" style="width: 200px; margin-left: 3%;">
        <b-icon  icon="arrow-up-circle-fill" aria-hidden="true"></b-icon>
        Fechar Janela
      </b-button>

      <b-button v-else @click="changeForm()" style="width: 200px;">
        <b-icon  icon="arrow-down-circle-fill" aria-hidden="true"></b-icon>
        Adicionar Anime
      </b-button>
    
    <b-form class="create-form" v-if="form_control.state" >
      

        <b-form-invalid-feedback :state="validation">
          Os Campos devem conter no minimo 2 caracteres.
        </b-form-invalid-feedback>
        <b-form-valid-feedback :state="validation">
          OK.
        </b-form-valid-feedback>
        <b-form-group >
          <b-form-input cols="8" 
          trim v-model="anime_add.title" 
          type="search" 
          id="input-live"
          placeholder="Anime"></b-form-input>
     
          <b-form-textarea cols="8" 
          trim v-model="anime_add.descricao" 
          type="text" 
          id="input-live"
          placeholder="Descrição"></b-form-textarea>

          <b-form-textarea cols="8"
          trim v-model="anime_add.img" 
          type="text" 
          id="input-live"
          placeholder="Url da Imagem"></b-form-textarea>
        </b-form-group>
        

        <b-button :disabled="!validation" @click="onSubmit()" >Save</b-button>
      
    </b-form>
    </b-col>
  </div>
</template>

<script>
import { mapActions } from "vuex";
export default {
  name: "AddAnime",
  data() {
    return {
      form_control: {
        state: false,
      },
      anime_add :{
        title: "",
        descricao: "",
        img: ""
      }
    };
  },
  methods: {
    ...mapActions(["addAnime"]),
    onSubmit() {
      if(this.validation){
        this.addAnime(this.anime_add);
        this.reset()
      }
    },
    changeForm() {
      if (this.form_control.state){
        this.form_control.state = false
      }
      else {
        this.form_control.state = true
      }
    },
    reset(){
      this.anime_add.title = ""
      this.anime_add.descricao = ""
      this.anime_add.img = ""
    }
  },
  computed: {
    validation() {
      return this.anime_add.title.length > 2 && this.anime_add.descricao.length > 2
    }
  }
};
</script>

<style>
.container{
  margin-left: 34%;
}

#input-live{
  margin-top: 3%;
}
.create-form{
  margin: 3%;
}
</style>